@import "./../../config/variables.scss";

.RootDivision {
    background-color: black;
    .OutterDivision {
      color: white;
      max-width: $max-page-width;
      font-size: 1em;
      margin: auto;
      padding: 4em 0em 1em 1em;
      .GoBackDivision {
        background-color: $nav-bg-model-selected-color;
        padding: 1em;
        margin: 1em 0;
        .Text {
            text-align: center;
            a {
                cursor: pointer;
                color: white;
            }
        }
      }
      .TitleDivision {
          margin-bottom: 0.5em;
          display: flex;
          flex-direction: row;
          align-items: flex-end;
          justify-content: flex-start;
          .TitleText {
              font-weight: bold;
              font-size: 1.5em;
          }
          .DescriptionText {
              margin-left: 1em;
          }
      }
      .Divider {
          margin: 0em;
          padding: 0em;
          border-width: 0.03em !important;
          border-color: $dark-gray !important;
      }
    }
}

