@import "./../../config/variables.scss";
.RootDivision {
    color: white;
    max-width: $max-page-width;
    font-size: 1em;
    margin: auto;
    .OutterDivision {
        .InnerDivision {
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: space-around;
          margin: 3em;
          .CardDivision {
              .Card {
                  width: 500px;
                  padding: 1em;
                  background-color: transparent;
                  color: white;
                  font-size: 1em;
                  margin: 0em 2em;
                  .ImageDIvision {
                      width: 100%;
                      height: 20em;
                      background-color: #505255;
                  }
                  .TitleDivision {
                      flex-direction: column;
                      align-items: center;
                      justify-content: center;
                      margin-top: 1em;
                      .TitleText {
                          margin:0em;
                          padding:0em;
                          font-size: 2em;
                          text-align: center;
                      }
                      .SubTitleText {
                          color: $dark-gray;
                          text-align: center;
                      }
                  }
                  .DescriptionDivision {
                      margin: 1em 0em;
                      .Text {
                          color: $dark-gray;
                      }
                  }
                  .ButtonDivision {
                      .Button {
                          cursor: pointer;
                          border: 1px solid $yellow;
                          width: 8em;
                          .Text {
                              padding: 0.5em;
                              font-weight: bold;
                              color: $yellow;
                              text-align: center;
                          }
                      }
                  }
              }
          }
      }
    }
}

